<div><v-layout><v-app-bar absolute style="overflow: visible"><v-app-bar-nav-icon class="navbarcolor" id="btnnavbar" @click="clickdrawer()"></v-app-bar-nav-icon><v-app-bar-title><a>Accueil</a></v-app-bar-title></v-app-bar><v-navigation-drawer id="navigationdrawer" v-model="drawer" absolute temporary><v-list nav dense><div v-for="item in redirectitems"><v-list-item :prepend-icon="item.icon" :title="item.title" @click="redirectnavbar(item.path)"></v-list-item></div></v-list></v-navigation-drawer><v-main><v-card class="mx-auto overflow-hidden" height="100vh" width="0vw"></v-card></v-main><div id="main"><v-card id="ecartcategorie" elevation="0"></v-card><v-card id="ecartcategorie" elevation="0"></v-card><div style="width: 90vw;"><v-card width="80%"><v-card-text><b><br><br><p>Bonjour, et bienvenue sur ma page personnelle.</p><br><p>Je m'appelle Romain, alias Zyco si vous me cherchez sur les réseaux sociaux.</p><br><p>Passionné par le développement informatique depuis mes 12 ans, je recherche aujourd'hui une alternance à Bac+2 ou 3 pour évoluer dans le développement logiciel.</p><br><p>Et puisque juste après le bac, mon futur employeur se posera naturellement la question de ma valeur ajoutée pour son entreprise, ce mini site sans prétention a vocation à répondre au maximum <u><a @click="redirectnavbar('/faq')">des questions</a></u><a> qu'il peut se poser.</a></p><br><li>Que sais-je déjà faire?</li><li>Quel est mon projet professionnel, quelles sont mes motivations?</li><li>Une alternance, oui, mais ou et quand, dans quelles conditions?</li><li>Et bien sur, pourquoi me choisir moi plutôt qu'un autre?</li><br><p>Professionnel, recruteur, ou simple curieux, je vous souhaite donc une bonne navigation, en espérant que vous trouverez ici de quoi satisfaire toutes vos interrogations.</p><br><p>Convaincu ? Faites circuler et n'hésitez pas à me contacter, pour un RdV, une question, un encouragement, une recommandation ou un conseil.</p><br><p>Toujours sceptique? Laissez-moi aussi, si vous le souhaitez, un commentaire !</p><br><p>Et rendez-vous dans 10 ans!</p><br></b></v-card-text></v-card></div></div></v-layout></div>